import React, { useRef, useEffect } from 'react'
import CardSlider from '../../Cards/CardSlider'
import { handleThumbDrag, handleMouseDown, handleScroll, initialMouseX, initialThumbX, isDragging } from '../../../helpers/scroll'

export default function HowItWorks() {
  const sliderRef = useRef<HTMLDivElement>(null)
  const thumbRef = useRef<HTMLDivElement>(null)
  const trackRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const thumb = thumbRef.current
    if (!thumb) return

    const handleMouseDownWrapper = (e: MouseEvent) => handleMouseDown(e, thumbRef, (event) => handleThumbDrag(event, sliderRef, thumbRef, trackRef))

    thumb.addEventListener('mousedown', handleMouseDownWrapper)
    return () => {
      thumb.removeEventListener('mousedown', handleMouseDownWrapper)
    }
  }, [])

  useEffect(() => {
    const slider = sliderRef.current
    if (!slider) return

    const handleScrollWrapper = () => handleScroll(sliderRef, thumbRef, trackRef)

    slider.addEventListener('scroll', handleScrollWrapper)
    return () => {
      slider.removeEventListener('scroll', handleScrollWrapper)
    }
  }, [])

  return (
    <section id='how_it_works' className='w-full mt-36 select-none scroll-mt-[6rem]'>
      <div className='container mx-auto flex-col'>
        <h2 className='font-sans text-black dark:text-white text-3xl lg:text-5xl font-medium mb-8'>
            Tail Talks?
        </h2>
        <div className='relative overflow-hidden rounded-[2rem]'>
          <div ref={sliderRef} className='flex overflow-x-auto no-scrollbar'>
            <CardSlider bg={'bg-step-1'} title={' 1'} text={'   '} />
            <CardSlider bg={'bg-step-2'} title={' 2'} text={'       '} />
            <CardSlider bg={'bg-step-3'} title={' 3'} text={'      '} />
            <CardSlider bg={'bg-step-4'} title={' 4'} text={'     '} />
            <CardSlider bg={'bg-step-5'} title={' 5'} text={'     '} />
            <CardSlider bg={'bg-step-6'} title={' 6'} text={' NFT  '} />
            <CardSlider bg={'bg-step-7'} title={' 7'} text={'       !'} />
            <CardSlider bg={'bg-step-8'} title={' 8'} text={'      '} />
            <CardSlider bg={'bg-step-9'} title={' 9'} text={'     '} />
            <CardSlider bg={'bg-step-10'} title={' 10'} text={'     '} />
          </div>
        </div>
        {/*  */}
        <div className='relative mt-5'>
          <div ref={trackRef} className='w-[320px] md:w-[550px] lg:w-[700px] mx-auto h-1 bg-[#4D4D4D] rounded relative'>
            <div
              ref={thumbRef}
              className='h-2 bg-black-80 dark:bg-white rounded cursor-pointer w-[100px] md:w-[200px] absolute left-0 top-[-2px]'
            ></div>
          </div>
        </div>
        {/*   */}
      </div>
    </section>
  )
}
